table {
  width: 850px;
  margin: 0 auto 35px;
  border-collapse: collapse;
  font-size: 15px;
}

table th, td {
  border: 1px solid #2583b7;
  padding: 8px 15px;
  text-align: center;
}

table th {
  background-color: #c6ebff;
  color: #0f336a;
  font-size: 1.2em;
  font-weight: 300;
}