form {
  width: 650px;
  margin: 30px auto;
  padding: 20px;
  border: 1px dashed #71b8ff;
  border-radius: 10px;
}

input[type="text"],
textarea {
  width: 100%;
  padding: 10px;
  margin-top: 10px;
  margin-bottom: 15px;
  border: 1px solid #71b8ff;
  border-radius: 10px;
  font-size: 1em;
  box-sizing: border-box;

  &:focus {
    border: 2px solid #275a94;
  }
}

textarea {
  width: 100%;
  height: 500px;
}

input[type="submit"] {
  width: 100%;
  padding: 10px;
  border: none;
  border-radius: 10px;
  font-size: 1em;
  cursor: pointer;
  background-color: #275a94;
  border-radius: 10px;
  color: #fff;

  &:hover {
    background-color: #fff;
    color: #275a94;
    transition: 0.2s;
    cursor: pointer;
  }
}